html, body {
  height: 100%;
  width: 100%;
  margin: 0;
}

body {
  min-height: 100%;
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  font-family: Roboto, sans-serif;
  display: flex;
}

h1, h2, h3, h4, h5 {
  text-align: center;
}

#demo {
  width: 800px;
  height: 600px;
  max-width: 90vw;
  max-height: 70vh;
  background-color: #000;
  border: 4px solid #aaa;
  border-radius: 12px;
}

.title {
  justify-content: center;
  align-items: center;
  margin-top: 10vmin;
  display: flex;
}

.logo {
  height: 1.4em;
  margin-right: .5em;
  display: inline-block;
}

.labels {
  margin-bottom: 4px;
}

.disclaimer {
  margin-top: 8px;
  font-size: .9em;
}

.github-fork {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

@media only screen and (width <= 600px) {
  .logo, .github-fork {
    display: none;
  }
}

